
/** Return leghtened hex color string (starts with `#`). */
export const lighten = (col: string, value: number) => {
  let pound = false;
  let num =  parseInt(col, 16);
  if (col[0] === '#') {
    pound = true;
    num = parseInt(col.slice(1, col.length), 16);
  }

  let r = (num >> 16) + value;

  if (r > 255) {
    r = 255;
  }
  else if (r < 0) {
    r = 0;
  }

  let b = ((num >> 8) & 0x00FF) + value;

  if (b > 255) {
    b = 255;
  }
  else if (b < 0) {
    b = 0;
  }

  let g = (num & 0x0000FF) + value;

  if (g > 255) {
    g = 255;
  }
  else if (g < 0) {
    g = 0;
  }

  return `${ pound ? '#' : '' }${ (g | (b << 8) | (r << 16)).toString(16) }`;
};

/** Return darkened hex color string (starts with `#`). */
export const darken = (col: string, val: number) => {
  return lighten(col, -val);
};
