import * as React from 'react';

import { ModalBase, ModalBaseProps, ModalBaseState } from './ModalBase';

export interface ModalProps extends ModalBaseProps {}

interface ModalState extends ModalBaseState {
  open: boolean;
}

export class Modal extends ModalBase<ModalProps, ModalState> {

  constructor(props: ModalProps) {
    super(props);

    const { trigger } = props;

    this.state = {
      ...this.state,
      open: trigger ? false : true,
    };
  }

  protected modalIsOpen = () => {
    return this.state.open;
  }

  protected open = () => {
    this.setState({
      entryOutAnimation: this.skipEntryOutAnimation ? undefined : this.animationStyleSheets.entryAnimation,
      open: true,
    });
  }

  protected close = () => {
    this.setState({ entryOutAnimation: this.animationStyleSheets.outAnimation }, () => {
      setTimeout(() => this.setState({ open: false }), this.skipEntryOutAnimation ? 0 : 1000 * this.entryOutAnimationDuration);
    });
  }
}
