import * as React from 'react';
import { css, StyleSheet } from 'aphrodite';
import classnames from 'classnames';

import { ElementProps } from '../components/Element';
import { ButtonPosition } from '../../WidgetOptions';
import { button, fontSizes, zIndexTop } from '../../styles';
import { lighten } from '../../lib';
import { Icon, IconName } from '../../icons';

export interface ButtonTriggerProps extends ElementProps {
  // color: string;
  backgroundColor: string;
  position: ButtonPosition;
  icon: IconName;
  value?: string;
}

export const ButtonTrigger = (props: ButtonTriggerProps) => {
  const {
    value,
    className,
    backgroundColor,
    // color,
    position,
    icon,
    ...rest
  } = props;

  const buttonOffset = 0;
  const translateXInitialEM = position === 'left' ? -0.3 : 0.3;

  const styles = StyleSheet.create({
    container: {
      ...button,
      position: 'fixed',
      right: position === 'right' ? buttonOffset : undefined,
      left: position === 'left' ? buttonOffset : undefined,
      borderRadius: position === 'right' ? '2px 0 0 2px' : '0 2px 2px 0',
      bottom: '50%',
      backgroundColor,
      // color,
      fontSize: fontSizes.normal,
      zIndex: zIndexTop - 1,
      width: '2em',
      padding: position === 'left' ? '.5em .5em .5em .7em' : '.5em .7em .5em .5em',
      transform: `translateX(${ translateXInitialEM }em)`,
      height: '1.7em',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      cursor: 'pointer',
      ':hover': {
        backgroundColor: lighten(backgroundColor, 20),
        animationName: { from: { transform: `translateX(${ translateXInitialEM }em)` }, to: { transform: 'translateX(0)' } },
        animationDuration: '0.2s',
        animationFillMode: 'forwards',
        animationTimingFunction: 'ease-in-out',
      },
    },
  });

  const content = value || <Icon name={ icon } />;

  return (
    <div { ...rest } className={ classnames(className, css(styles.container)) }>{ content }</div>
  );
};
