import * as React from 'react';
import { StyleSheet, css } from 'aphrodite';
import classnames from 'classnames';

import {
  Animation,
} from '../../WidgetOptions';
import {
  isFirefox,
  isIE11,
  isNil
} from '../../lib';
import { Icon } from '../../icons';
import { animations } from '../../animations';
import { centeredBox, dialog } from '../../styles';
import { ContainerProps } from './Element';
import { rotateCloseBtnAnimation } from './animations';

const ie11OverflowScrollFix = isIE11() ? {
  '@media (max-height: 30em)': {
    flex: '1 0 15em',
  },
} : {};

export interface ModalBaseProps extends ContainerProps {
  backgroundColor: string;
  animation: Animation;
  closeIconColor: string;
  trigger?: React.ReactElement;
}

export interface ModalBaseState {
  entryOutAnimation: any;
  closeBtnHoverAnimation: any;
}

export abstract class ModalBase<P extends ModalBaseProps, S extends ModalBaseState = ModalBaseState> extends React.Component<P, S> {
  protected styles: any;
  protected animationStyleSheets: any;
  protected entryOutAnimationDuration: number;
  protected skipEntryOutAnimation: boolean;

  constructor(props: P) {
    super(props);

    const {
      backgroundColor,
      animation,
      closeIconColor,
    } = props;

    // Firefox has troubles with css transform: translate
    this.skipEntryOutAnimation = isFirefox() && animation === 'slideInOut';

    ////////////////////////////////////////////// Styles /////////////////////////////////////////

    this.styles = StyleSheet.create({
      modalContent: {
        margin: '0 1em 0 1em',
        overflow: 'auto',
        ...ie11OverflowScrollFix,
      },
      closeIcon: {
        marginLeft: 'auto',
        cursor: 'pointer',
        color: closeIconColor,
      },
      // @ts-ignore
      container: {
        ...centeredBox,
        ...dialog,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        backgroundColor,
        padding: '.5em',
        borderRadius: '5px',
        minWidth: '18em',
        maxWidth: '100vw',
        maxHeight: '100vh',
      },
      hidden: {
        display: 'none',
      },
      visible: {
        display: 'flex',
      },
    });

    //////////////////////////////////////////////////////// Animations ////////////////////////////////

    let entryAnimationKeyFrames;
    let outAnimationKeyFrames;
    let entryOutAnimationDuration = 0;
    switch (animation) {
      case 'none':
        break;
      case 'bounce':
        entryAnimationKeyFrames = animations.bounce;
        entryOutAnimationDuration = 1;
        break;
      case 'fadeInOut':
        entryAnimationKeyFrames = animations.fadein;
        outAnimationKeyFrames = animations.fadeout;
        entryOutAnimationDuration = 1;
        break;
      case 'slideInOut':
        entryAnimationKeyFrames = animations.slidein;
        outAnimationKeyFrames = animations.slideout;
        entryOutAnimationDuration = 2;
        break;
      default:
        break;
    }

    const initialTranslate = { tx: -50, ty: -50 };

    // TODO: when screen orientation changes from vertical to horizontal it
    // doesn't reflect in stylesheets; sometimes slideInOut animation doesn't
    // move widget out of the screen because of stale sizes in stylesheets

    let entryAnimation = {};
    if (entryAnimationKeyFrames) {
      entryAnimation = {
        animationName: typeof entryAnimationKeyFrames === 'function' ? entryAnimationKeyFrames(initialTranslate) : entryAnimationKeyFrames,
        animationDuration: `${ entryOutAnimationDuration }s`,
        animationFillMode: 'forwards',
      };
    }
    let outAnimation = {};
    if (outAnimationKeyFrames) {
      outAnimation = {
        animationName: typeof outAnimationKeyFrames === 'function' ? outAnimationKeyFrames(initialTranslate) : outAnimationKeyFrames,
        animationDuration: `${ entryOutAnimationDuration }s`,
        animationFillMode: 'forwards',
      };
    }

    this.entryOutAnimationDuration = entryOutAnimationDuration;

    const {
      rotateAnimation,
      rotateBackAnimation,
    } = rotateCloseBtnAnimation;



    this.animationStyleSheets = StyleSheet.create({
      // @ts-ignore
      entryAnimation,
      // @ts-ignore
      outAnimation,
      rotateAnimation,
      rotateBackAnimation,
    });

    // @ts-ignore
    this.state = {
      // open: trigger ? false : true,
      entryOutAnimation: this.animationStyleSheets.entryAnimation,
      closeBtnHoverAnimation: undefined,
    };
  }

  protected abstract modalIsOpen(): boolean;

  render() {
    const { children, className, trigger } = this.props;
    const { entryOutAnimation, closeBtnHoverAnimation } = this.state;
    const open = this.modalIsOpen();

    if (!open) {
      if (!trigger) {
        return null;
      }

      return <trigger.type { ...trigger.props } onClick={ this.open } />;
    }

    return (
      <>
        <div
          className={ classnames(className, css(this.styles.container, entryOutAnimation)) }
        >
          <div
            className={ css(this.styles.closeIcon, closeBtnHoverAnimation) }
            onClick={ this.close }
            onMouseOver={ this.handleCloseBtnMouseOver }
            onMouseOut={ this.handleCloseBtnMouseOut }
          >
            <Icon name='cross' />
          </div>
          <div className={ css(this.styles.modalContent) }>{ children }</div>
        </div>
        { trigger && <trigger.type { ...trigger.props } onClick={ this.open } /> }
      </>
    );
  }

  protected abstract open(): void;

  protected abstract close(): void;

  private handleCloseBtnMouseOver = () => {
    this.setState({ closeBtnHoverAnimation: this.animationStyleSheets.rotateAnimation });
  }

  private handleCloseBtnMouseOut = () => {
    this.setState({ closeBtnHoverAnimation: this.animationStyleSheets.rotateBackAnimation });
  }
}
