
/** Return a new object which doesn't contain given keys. */
export const exclude = <T>(obj: T, keys: Array<keyof T>) => {
  const filtered = {} as any;
  Object.keys(obj).forEach((key) => {
    if (keys.indexOf(key as keyof T) === -1) {
      filtered[key] = obj[key as keyof T];
    }
  });

  return filtered;
};
