import * as React from 'react';
import { css, StyleSheet } from 'aphrodite';

import { Input, FormProps, InputElement } from '../components';
import { find, createId, has } from '../../lib';
import { SubscribeWidgetOptions } from './Subscribe';

export interface SubscribeFormProps extends FormProps {
  widgetOptions: SubscribeWidgetOptions;
}

interface SubscribeFormState {
  email?: string;
  name?: string;
  consent?: boolean;
}

export class BaseSubscribeForm<P extends SubscribeFormProps = SubscribeFormProps> extends React.Component<P, SubscribeFormState> {
  protected styles: any;
  protected emailInput: any;
  protected nameInput: any;
  protected consentCheckbox: any;
  protected consentInputId: string;

  constructor(props: P) {
    super(props);

    this.state = {};

    const {
      contentOptions,
      appearanceOptions,
      integrationsOptions,
    } = props.widgetOptions;
    const {
      inputFields,
    } = contentOptions;
    this.emailInput = find(inputFields, (el) => el.name === 'email');
    this.nameInput = find(inputFields, (el) => el.name === 'name');
    this.consentCheckbox = find(inputFields, (el) => el.name === 'consent');
    this.consentInputId = createId('consent');
  }

  private handleChange = (e: React.SyntheticEvent<InputElement>) => {
    const { name, value } = e.currentTarget;

    if (has(this.state, name)) {
      this.setState({ [name]: value });
    }
  }

  private handleSubmit = (e: React.SyntheticEvent<InputElement>) => {
    // submit form
  }

  private handleConsentChange = (e: React.SyntheticEvent) => {
    this.setState((prevState: any) => {
      return { consent: !prevState.consent };
    });
  }

  render() {
    const {
      widgetOptions,
      ...rest
    } = this.props;
    const { buttonText } = widgetOptions.contentOptions;

    return (
      <form { ...rest }>
        {
          this.emailInput &&
          <Input
            { ...this.emailInput }
            className={ css(this.styles.input, this.styles.emailInput) }
            onChange={ this.handleChange }
            value={ this.state.email }
          />
        }
        {
          this.nameInput &&
          <Input
            { ...this.nameInput }
            className={ css(this.styles.input, this.styles.nameInput) }
            onChange={ this.handleChange }
            value={ this.state.name }
          />
        }
        { this.consentCheckbox &&
          <div className={ css(this.styles.input, this.styles.consent) }>
            <Input
              { ...this.consentCheckbox }
              className={ css(this.styles.consentCheckBox) }
              labelClassName={ css(this.styles.consentLabel) }
              id={ this.consentInputId }
              onChange={ this.handleConsentChange }
              checked={ this.state.consent }
            />
          </div>
        }
        <Input type='submit' name='submit' value={ buttonText } className={ css(this.styles.submitButton) } />
      </form>
    );
  }
}
