import * as React from 'react';
import { css, StyleSheet } from 'aphrodite';
import classnames from 'classnames';

import { ContainerProps } from './Element';
import { Icon } from '../../icons';
import { fontSizes } from '../../styles';
import { darken } from '../../lib';

export interface AccordionProps extends ContainerProps {
  position: 'left' | 'right';
  title: string;
  titleColor: string;
  backgroundColor: string;
  /** Accordion content height in px. */
  containerSize: 'small' | 'medium' | 'large';
  dividerColor: string;
  contentClassName?: string;
}

interface AccordionState {
  open: boolean;
  containerOpenCloseStyle: any;
  openCloseBtnMouseOver: boolean;
}

const containerHeightMap = {
  'small': 200,
  'medium': 400,
  'large': 500,
};

export class Accordion extends React.Component<AccordionProps, AccordionState> {
  private styles: any;

  constructor(props: AccordionProps) {
    super(props);

    const { position, backgroundColor, titleColor, containerSize, dividerColor } = props;

    const containerHeight = containerHeightMap[containerSize];

    const shift = 80;
    const height = 50; //px

    this.styles = StyleSheet.create({
      container: {
        position: 'fixed',
        top: window.innerHeight,
        left: position === 'left' ? shift : undefined,
        right: position === 'right' ? shift : undefined,
        backgroundColor,
        display: 'flex',
        flexDirection: 'column',
        borderRadius: '2px 2px 0 0',
      },
      containerInitial: {
        transform: `translateY(${ -height }px)`,
      },
      accordionOpen: {
        animationName: {
          from: { transform: `translateY(${ -height }px)` },
          to: { transform: `translateY(${ -height - containerHeight }px)` },
        },
        animationDuration: '.5s',
        animationFillMode: 'forwards',
      },
      accordionClose: {
        animationName: {
          from: { transform: `translateY(${ -height - containerHeight }px)` },
          to: { transform: `translateY(${ -height }px)` },
        },
        animationDuration: '.5s',
        animationFillMode: 'forwards',
      },
      mainContent: {
        height: containerHeight,
        padding: '.8em',
        overflow: 'auto',
      },
      header: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        height: `${ height }px`,
        borderBottom: `1px solid ${ dividerColor }`,
        padding: '0 .8em',
      },
      title: {
        margin: 0,
        padding: 0,
        color: titleColor,
        fontSize: fontSizes.normal,
      },
      openCloseBtn: {
        marginLeft: '1.5em',
        cursor: 'pointer',
        color: titleColor,
      },
      chevronUp: {
        transform: 'rotate(0deg)',
      },
      chevronDown: {
        transform: 'rotate(180deg)',
      },
    });

    this.state = {
      open: false,
      containerOpenCloseStyle: this.styles.containerInitial,
      openCloseBtnMouseOver: false,
    };
  }

  render() {
    const { children, className, title, titleColor, contentClassName } = this.props;
    const { open, containerOpenCloseStyle, openCloseBtnMouseOver } = this.state;

    const chevronDirectionStyle = open ? this.styles.chevronDown : this.styles.chevronUp;
    const openCloseBtn = <Icon onMouseOver={ this.onOpenCloseBtnMouseOver } onMouseOut={ this.onOpenCloseBtnMouseOut } name='chevronUp' />;

    return (
      <div className={ classnames(className, css(this.styles.container, containerOpenCloseStyle)) }>
        <div className={ css(this.styles.header) }>
          <h2 className={ css(this.styles.title) }>{ title }</h2>
          <div className={ css(this.styles.openCloseBtn, chevronDirectionStyle) } onClick={ this.toggleOpenClose }>{ openCloseBtn }</div>
        </div>
        <div className={ classnames(css(this.styles.mainContent), contentClassName) }>
          { children }
        </div>
      </div>
    );
  }

  private onOpenCloseBtnMouseOver = () => {
    this.setState({ openCloseBtnMouseOver: true });
  }

  private onOpenCloseBtnMouseOut = () => {
    this.setState({ openCloseBtnMouseOver: false });
  }

  private toggleOpenClose = () => {
    this.setState((prevState) => {
      return {
        open: !prevState.open,
        containerOpenCloseStyle: prevState.open ? this.styles.accordionClose : this.styles.accordionOpen,
      };
    });
  }
}
