import * as React from 'react';
import { css, StyleSheet } from 'aphrodite';
import classnames from 'classnames';

import { ElementProps } from './Element';

const styles = StyleSheet.create({
  textarea: {
    resize: 'none',
  },
});

export interface CommonInputProps extends ElementProps {
  type: InputFieldType;
  name: string;
  labelClassName?: string;
  required?: boolean;
  label?: string;
  value?: string;
  onChange?: (e: React.SyntheticEvent) => void;
}

export interface InputProps extends CommonInputProps, TextareaProps, SelectProps {
  autocomplete?: boolean;
}

export type InputFieldType = 'text' | 'email' | 'checkbox' | 'select' | 'submit' | 'reset' | 'textarea';

export interface OptionConfig {
  value: string;
  selected?: boolean;
  className?: string;
}

export const Input = (props: InputProps) => {
  const {
    type,
    name,
    labelClassName,
    required,
    autocomplete,
    placeholder,
    label,
    value,
    multiple,
    options,
    id,
    className,
  } = props;

  if (type === 'select' && Array.isArray(options)) {
    return <Select { ...props } />;
  }

  if (type === 'textarea') {
    return <Textarea { ...props } />;
  }

  const autocompleteValue = autocomplete === true ? 'on' : autocomplete === false ? 'off' : undefined;
  const listeners = extractEventListeners(props);

  return (
    <>
      { label && id && <label className={ labelClassName } htmlFor={ id }>{ label }</label> }
      <input
        { ...listeners }
        type={ type }
        name={ name }
        required
        placeholder={ placeholder }
        value={ value }
        id={ id }
        className={ className }
        autoComplete={ autocompleteValue }
      />
    </>
  );
};

interface OptionProps {
  value: string;
  selected?: boolean;
  className?: string;
}

const Option = (props: OptionProps) => {
  const {
    value,
    selected,
    className,
  } = props;

  return <option value={ value } selected className={ className }>{ value }</option>;
};

interface TextareaProps extends CommonInputProps {
  placeholder?: string;
  rows?: number;
  cols?: number;
  maxLength?: number;
}

const Textarea = (props: TextareaProps) => {
  const {
    name,
    required,
    value,
    placeholder,
    rows,
    cols,
    maxLength,
    className,
    id,
    label,
    labelClassName,
  } = props;

  const listeners = extractEventListeners(props);
  return (
    <>
      { label && id && <label className={ labelClassName } htmlFor={ id }>{ label }</label> }
      <textarea
        { ...listeners }
        name={ name }
        required
        value={ value }
        placeholder={ placeholder }
        id={ id }
        className={ classnames(className, css(styles.textarea)) }
        rows={ rows }
        cols={ cols }
        maxLength={ maxLength }
      />
    </>
  );
};

interface SelectProps extends CommonInputProps {
  multiple?: boolean;
  options?: OptionProps[];
}

const Select = (props: SelectProps) => {
  const {
    name,
    labelClassName,
    required,
    label,
    value,
    multiple,
    options,
    id,
    className,
  } = props;
  const listeners = extractEventListeners(props);

  return (
    <>
      { label && id && <label className={ labelClassName } htmlFor={ id }>{ label }</label> }
      <select { ...listeners } name={ name } required multiple id={ id } className={ className } value={ value }>
        { ...(options || []).map((option) => <Option { ...option } />) }
      </select>
    </>
  );
};

const extractEventListeners = (props: any) => {
  const extract = {} as any;
  Object.keys(props).forEach((key) => {
    if (key.slice(0, 2).toLowerCase() === 'on') {
      extract[key] = props[key];
    }
  });

  return extract;
};
