import * as React from 'react';

import { ModalBase, ModalBaseProps } from './ModalBase';

export interface ModalControledProps extends ModalBaseProps {
  open: boolean;
  onClose: () => void;
}

export class ModalControled extends ModalBase<ModalControledProps> {

  protected modalIsOpen = () => {
    return this.props.open;
  }

  protected open = () => {
    this.setState({
      entryOutAnimation: this.skipEntryOutAnimation ? undefined : this.animationStyleSheets.entryAnimation,
    });
  }

  protected close = () => {
    this.setState({ entryOutAnimation: this.animationStyleSheets.outAnimation }, () => {
      setTimeout(() => {
        this.props.onClose();
      }, this.skipEntryOutAnimation ? 0 : 1000 * this.entryOutAnimationDuration);
    });
  }
}
