
export const join = (base: string, ...rest: string[]): string => {
  const baseNorm = base[base.length - 1] === '/' ? base.slice(0, base.length - 1) : base;
  const restNorm = [] as string[];
  rest.forEach((part) => {
    let norm = part;
    if (norm[0] === '/') {
      norm = norm.slice(1, norm.length - 1);
    }
    if (norm[norm.length - 1] === '/') {
      norm = norm.slice(norm.length - 1);
    }
    restNorm.push(norm);
  });

  return [ baseNorm, ...restNorm ].join('/');
};
