import * as React from 'react';

import { BarBase, BarBaseProps, BarBaseState } from './BarBase';

export interface BarProps extends BarBaseProps {}

interface BarState extends BarBaseState {
  open: boolean;
}

export class Bar extends BarBase<BarProps, BarState> {

  constructor(props: BarProps) {
    super(props);

    this.state = {
      ...this.state,
      open: true,
    };
  }

  protected isOpen = () => {
    return this.state.open;
  }

  protected close = () => {
    this.setState({ entryOutAnimation: this.animationStyleSheets.outAnimation }, () => {
      setTimeout(() => this.setState({ open: false }), 1000 * this.entryOutAnimationDuration);
    });
  }
}
