import * as React from 'react';
import { css, StyleSheet } from 'aphrodite';

import { Input, FormProps, InputElement } from '../components';
import { find, createId, has } from '../../lib';
import { ContactWidgetOptions } from './Contact';

export interface ContactFormProps extends FormProps {
  widgetOptions: ContactWidgetOptions;
  styleSheet: any;
}

interface ContactFormState {
  email?: string;
  name?: string;
  message?: string;
}

export class ContactForm extends React.Component<ContactFormProps, ContactFormState> {
  protected styles: any;
  protected emailInput: any;
  protected nameInput: any;
  protected messageInput: any;

  constructor(props: ContactFormProps) {
    super(props);

    this.state = {};
    const { styleSheet } = props;

    const {
      contentOptions,
      appearanceOptions,
      integrationsOptions,
    } = props.widgetOptions;
    const {
      inputFields,
    } = contentOptions;
    this.emailInput = find(inputFields, (el) => el.name === 'email');
    this.nameInput = find(inputFields, (el) => el.name === 'name');
    this.messageInput = find(inputFields, (el) => el.name === 'message');

    this.styles = styleSheet;
  }

  private handleChange = (e: React.SyntheticEvent<InputElement>) => {
    const { name, value } = e.currentTarget;

    if (has(this.state, name)) {
      this.setState({ [name]: value });
    }
  }

  private handleSubmit = (e: React.SyntheticEvent<InputElement>) => {
    // submit form
  }

  render() {
    const {
      widgetOptions,
      styleSheet,
      ...rest
    } = this.props;
    const { buttonText } = widgetOptions.contentOptions;

    return (
      <form { ...rest }>
        {
          this.emailInput &&
          <Input
            { ...this.emailInput }
            className={ css(this.styles.input, this.styles.emailInput) }
            onChange={ this.handleChange }
            value={ this.state.email }
          />
        }
        {
          this.nameInput &&
          <Input
            { ...this.nameInput }
            className={ css(this.styles.input, this.styles.nameInput) }
            onChange={ this.handleChange }
            value={ this.state.name }
          />
        }
        {
          this.messageInput &&
          <Input
            { ...this.messageInput }
            className={ css(this.styles.input, this.styles.messageInput) }
            onChange={ this.handleChange }
            value={ this.state.name }
          />
        }
        <Input type='submit' name='submit' value={ buttonText } className={ css(this.styles.submitButton) } />
      </form>
    );
  }
}
