import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { css, StyleSheet } from 'aphrodite';
import * as _ from 'lodash';

import {
  WidgetType,
} from '../src/WidgetOptions';

import { SubscribeConfigForm } from './SubscribeConfigForm';
import { ContactConfigForm } from './ContactConfigForm';
import { PromoConfigForm } from './PromoConfigForm';
import { SiteWidgetsWindow } from '../src/entryPoints/types';

declare const window: SiteWidgetsWindow;

class Preview extends React.Component<{}, { widget: WidgetType }> {
  constructor(props: any) {
    super(props);

    this.state = {
      widget: 'Subscribe',
    };
  }

  render() {
    const { widget } = this.state as any;

    let configForm;

    switch(widget) {
      case 'Subscribe':
        configForm = <SubscribeConfigForm styles={ styles } />;
        break;
      case 'Contact':
        configForm = <ContactConfigForm styles={ styles } />;
        break;
      case 'Promo':
        configForm = <PromoConfigForm styles={ styles } />;
        break;
      default:
        configForm = <div>no widget selected</div>;
        break;
    }

    const options = [
      { name: 'Subscribe', value: 'Subscribe' },
      { name: 'Contact', value: 'Contact' },
      { name: 'Promo', value: 'Promo' },
      { name: 'Share', value: 'Share' },
      { name: 'Follow', value: 'Follow' },
      { name: 'Survey', value: 'Survey' },
    ];

    return (
      <>
        <form>
          <select name='widget' value={ this.state.widget } onChange={ this.handleChange }>
            { options.map((opt) => <option value={ opt.value } key={ opt.value }>{ opt.name }</option>) }
          </select>
        </form>
        { configForm }
      </>
    );
  }

  handleChange = (e: React.SyntheticEvent) => {
    if (e.target) {
      // @ts-ignore
      this.setState({ widget: e.target.value });
    }
  }
}

const styles = StyleSheet.create({
  form: {
    maxWidth: '400px',
    margin: '20px auto',
    display: 'flex',
    flexDirection: 'column',
  },
  input: {
    flex: '1 0 auto',
  },
  label: {
    display: 'flex',
    margin: '5px',
  },
});

ReactDOM.render(
  <Preview />,
  document.getElementById('root')
);
