import * as React from 'react';
import { StyleSheet, css } from 'aphrodite';

import {
  BaseWidgetOptions,
  SuccessPageOptions,
  IntegrationsOptions,
  DisplayTypeDesktop,
  DisplayTypeMobile,
} from '../../WidgetOptions';
import { Modal, InputProps, Bar, Accordion, ButtonTrigger, MobileButtonTrigger, ModalControled, BarControled } from '../components';
import { mediaBreakPointsCss, fontSizes, form, button, } from '../../styles';
import { getPlatform, isOneOf } from '../../lib';
import { WidgetRenderTemplate } from '../WidgetRenderTemplate';
import { ContactForm } from './ContactForm';
import { contactFormModalStyles, contactFormAccordionStyles } from './styles';

export interface ContactWidgetOptions extends BaseWidgetOptions {
  contentOptions: ContactWidgetContentOptions;

  integrationsOptions: IntegrationsOptions;
  successPageOptions: SuccessPageOptions;
}

export interface ContactWidgetContentOptions {
  title: string;
  description: string;
  inputFields: ContactWidgetInputField[];
  buttonText: string;
  triggerButtonText?: string;
}

export interface ContactWidgetInputField extends InputProps {}

export class Contact extends WidgetRenderTemplate<ContactWidgetOptions, { modalOpen: boolean, open: boolean }> {
  private styles: any;
  private readonly name = 'contact';
  private contactFormModalStyles: any;
  private contactFormBarStyles: any;
  private contactFormAccordionStyles: any;

  constructor(props: ContactWidgetOptions) {
    super(props);

    const {
      appearanceOptions,
    } = props;
    const {
      colors,
      font,
    } = appearanceOptions;

    const { medium, small } = mediaBreakPointsCss;

    this.styles = StyleSheet.create({
      container: {
        color: colors.text,
        fontFamily: font,
      },
      description: {
        maxWidth: '30em',
      },
      header: {
        color: colors.headerText,
      },
      headerBar: {
        margin: '0 50px 0 10px',
        [medium]: {
          display: 'none',
        },
      },
      // @ts-ignore
      modalForm: {
        ...form,
        width: '100%',
        minWidth: '20em',
        marginTop: '.1em',
        marginBottom: '.1em',
        flexDirection: 'column',
        [medium]: {
          minWidth: '10em',
        },
      },
      accordionContent: {
        display: 'flex',
        flexDirection: 'column',
      },
      // @ts-ignore
      accordionForm: {
        ...form,
        width: '100%',
        flex: '1 0 auto',
        minWidth: '20em',
        marginTop: '.1em',
        marginBottom: '.1em',
        flexDirection: 'column',
        [medium]: {
          minWidth: '10em',
        },
      },
      barModalTrigger: {
        ...button,
        backgroundColor: colors.button,
        color: colors.buttonText,
        fontSize: fontSizes.normal,
        padding: '.5em 1em',
      },
      note: {
        fontSize: fontSizes.small,
        fontWeight: 'bold',
        color: colors.secondaryText,
      },
      modalTrigger: {
        fontFamily: font,
        fontSize: fontSizes.normal,
        color: colors.buttonText,
      },
    });

    this.contactFormModalStyles = StyleSheet.create(contactFormModalStyles({ colors }) as any);
    this.contactFormBarStyles = this.contactFormModalStyles;
    this.contactFormAccordionStyles = StyleSheet.create(contactFormAccordionStyles({ colors }) as any);

    this.state = {
      modalOpen: false,
      open: true,
    };
  }

  protected renderModal = () => {
    const { appearanceOptions, contentOptions, integrationsOptions, displayTypeOptions } = this.props;
    const { displayTypeDesktop, displayTypeMobile, mobileButton } = displayTypeOptions;
    const { colors } = appearanceOptions;
    const {
      title,
      description,
    } = contentOptions;
    const formAction = integrationsOptions.webhooks[0].url;

    const desktop = getPlatform() === 'desktop';
    const mobile = getPlatform() === 'mobile';

    const instant = desktop && !isOneOf<DisplayTypeDesktop>(displayTypeDesktop, [
      'buttonLeft', 'buttonRight',
    ]) || mobile && !isOneOf<DisplayTypeMobile>(displayTypeMobile, [
      'modalTriggered',
    ]);

    let trigger;
    if (!instant) {
      if (desktop && isOneOf<DisplayTypeDesktop>(displayTypeDesktop, ['buttonLeft', 'buttonRight'])) {
        const position = displayTypeDesktop === 'buttonLeft' ? 'left' : 'right';
        trigger = (
          <ButtonTrigger
            className={ css(this.styles.modalTrigger) }
            backgroundColor={ colors.button }
            position={ position }
            icon='paperPlane'
          />
        );
      } else {
        trigger = (
          <MobileButtonTrigger
            className={ css(this.styles.modalTrigger) }
            value='click me'
            color={ colors.buttonText }
            backgroundColor={ colors.button }
            position={ mobileButton }
          />
        );
      }
    }

    return (
      <Modal
        className={ css(this.styles.container) }
        backgroundColor={ colors.background }
        animation={ appearanceOptions.animation }
        closeIconColor={ colors.text }
        trigger={ trigger }
      >
        <h2 className={ css(this.styles.header) }>{ title }</h2>
        <p className={ css(this.styles.description) }>{ description }</p>
        <ContactForm className={ css(this.styles.modalForm) } action={ formAction } method='post' widgetOptions={ this.props } name={ this.name } styleSheet={ this.contactFormModalStyles } />
      </Modal>
    );
  }

  /** Desktop only yet. */
  protected renderBar = () => {
    const { displayTypeOptions, appearanceOptions, contentOptions, integrationsOptions } = this.props;
    const { modalOpen, open } = this.state;

    const { displayTypeDesktop } = displayTypeOptions;
    const { colors } = appearanceOptions;
    const { title, description, triggerButtonText } = contentOptions;
    const formAction = integrationsOptions.webhooks[0].url;

    const barPosition = displayTypeDesktop === 'barTop' ? 'top' : 'bottom';

    return (
      <>
        <BarControled
          className={ css(this.styles.container) }
          position={ barPosition }
          animation={ appearanceOptions.animation }
          closeIconColor={ colors.text }
          open={ open }
          onClose={ this.close }
        >
          <h2 className={ css(this.styles.header, this.styles.headerBar) }>{ title }</h2>
          <button className={ css(this.styles.barModalTrigger) } onClick={ this.handleModalOpen }>{ triggerButtonText }</button>
        </BarControled>
        <ModalControled
          className={ css(this.styles.container) }
          backgroundColor={ colors.background }
          animation={ appearanceOptions.animation }
          closeIconColor={ colors.text }
          open={ modalOpen }
          onClose={ this.handleModalClose }
        >
          <h2 className={ css(this.styles.header) }>{ title }</h2>
          <p className={ css(this.styles.description) }>{ description }</p>
          <ContactForm className={ css(this.styles.modalForm) } action={ formAction } method='post' widgetOptions={ this.props } name={ this.name } styleSheet={ this.contactFormBarStyles } />
        </ModalControled>
      </>
    );
  }

  // desktop only yet
  protected renderAccordion = () => {
    const { appearanceOptions, displayTypeOptions, contentOptions, integrationsOptions } = this.props;
    const { displayTypeDesktop } = displayTypeOptions;
    const { title, description } = contentOptions;
    const { colors } = appearanceOptions;
    const formAction = integrationsOptions.webhooks[0].url;

    const position = displayTypeDesktop === 'accordionLeft' ? 'left' : 'right';

    return (
      <Accordion
        className={ css(this.styles.container) }
        position={ position }
        title={ title }
        titleColor={ colors.headerText }
        backgroundColor={ colors.background }
        containerSize='large'
        dividerColor={ colors.text }
        contentClassName={ css(this.styles.accordionContent) }
      >
        <p className={ css(this.styles.description) }>{ description }</p>
        <ContactForm className={ css(this.styles.accordionForm) } action={ formAction } method='post' widgetOptions={ this.props } name={ this.name } styleSheet={ this.contactFormAccordionStyles } />
      </Accordion>
    );
  }

  private handleModalOpen = () => {
    this.setState({ modalOpen: true });
  }

  private close = () => {
    this.setState({ open: false });
  }

  private handleModalClose = () => {
    this.setState({ modalOpen: false }, () => this.close());
  }
}
