import * as React from 'react';
import { css, StyleSheet } from 'aphrodite';
import classnames from 'classnames';

import { ContainerProps } from './Element';
import { dialog } from '../../styles';
import { rotateCloseBtnAnimation } from './animations';
import { Animation } from '../../WidgetOptions';
import { animations } from '../../animations';
import { Icon } from '../../icons';

export interface BarBaseProps extends ContainerProps {
  position: BarPosition;
  animation: Animation;
  closeIconColor: string;
}

export interface BarBaseState {
  entryOutAnimation: any;
  closeBtnHoverAnimation: any;
}

export type BarPosition = 'top' | 'bottom';

export abstract class BarBase<P extends BarBaseProps, S extends BarBaseState = BarBaseState> extends React.Component<P, S> {
  protected styles: any;
  protected animationStyleSheets: any;
  protected entryOutAnimationDuration: number;

  constructor(props: P) {
    super(props);

    const { position, animation, closeIconColor } = props;

    this.styles = StyleSheet.create({
      // @ts-ignore
      container: {
        ...dialog,
        left: 0,
        right: 0,
        top: position === 'top' ? 0 : undefined,
        bottom: position === 'bottom' ? 0 : undefined,
        height: '3em',
        backgroundColor: 'gray',
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
      },
      closeIcon: {
        marginLeft: 'auto',
        flex: '0 0 auto',
        cursor: 'pointer',
        marginRight: '2em',
        color: closeIconColor,
      },
      mainContent: {
        display: 'flex',
        justifyContent: 'center',
        flex: '1 0 auto',
        alignItems: 'center',
      },
    });

    const { rotateAnimation, rotateBackAnimation } = rotateCloseBtnAnimation;

    let entryAnimationKeyFrames;
    let outAnimationKeyFrames;
    let entryOutAnimationDuration = 0;
    switch (animation) {
      case 'none':
        break;
      case 'bounce':
        entryAnimationKeyFrames = animations.bounce;
        entryOutAnimationDuration = 1;
        break;
      case 'fadeInOut':
        entryAnimationKeyFrames = animations.fadein;
        outAnimationKeyFrames = animations.fadeout;
        entryOutAnimationDuration = 1;
        break;
      case 'slideInOut':
        entryAnimationKeyFrames = position === 'top' ? animations.slideinTopToBottom : animations.slideinBottomToTop;
        outAnimationKeyFrames = position === 'top' ? animations.slideoutBottomToTop : animations.slideoutTopToBottom;
        entryOutAnimationDuration = position === 'top' ? 0.8 : 1.9; // it seems much slower when with top bar
        break;
      default:
        break;
    }

    this.entryOutAnimationDuration = entryOutAnimationDuration;

    const initialTranslate = { tx: 0, ty: 0 };
    const approximateBarHeight = 70; // px

    let entryAnimation = {};
    if (entryAnimationKeyFrames) {
      entryAnimation = {
        animationName: typeof entryAnimationKeyFrames === 'function' ? entryAnimationKeyFrames(initialTranslate, approximateBarHeight) : entryAnimationKeyFrames,
        animationDuration: `${ entryOutAnimationDuration }s`,
        animationFillMode: 'forwards',
      };
    }
    let outAnimation = {};
    if (outAnimationKeyFrames) {
      outAnimation = {
        animationName: typeof outAnimationKeyFrames === 'function' ? outAnimationKeyFrames(initialTranslate, approximateBarHeight) : outAnimationKeyFrames,
        animationDuration: `${ entryOutAnimationDuration }s`,
        animationFillMode: 'forwards',
      };
    }

    this.animationStyleSheets = StyleSheet.create({
      rotateAnimation,
      rotateBackAnimation,
      // @ts-ignore
      entryAnimation,
      // @ts-ignore
      outAnimation,
    });

    // @ts-ignore
    this.state = {
      entryOutAnimation: this.animationStyleSheets.entryAnimation,
      closeBtnHoverAnimation: undefined,
    };
  }

  render() {
    const { className, closeIconColor, children } = this.props;
    const { closeBtnHoverAnimation, entryOutAnimation } = this.state;
    const open = this.isOpen();

    if (!open) {
      return null;
    }

    return (
      <div className={ classnames(className, css(this.styles.container, entryOutAnimation)) }>
        <div className={ css(this.styles.mainContent) }>
          { children }
        </div>
        <div
            className={ css(this.styles.closeIcon, closeBtnHoverAnimation) }
            onClick={ this.close }
            onMouseOver={ this.handleCloseBtnMouseOver }
            onMouseOut={ this.handleCloseBtnMouseOut }
          >
          <Icon name='cross' />
        </div>
      </div>
    );
  }

  protected abstract isOpen(): boolean;

  protected abstract close(): void;

  private handleCloseBtnMouseOver = () => {
    this.setState({ closeBtnHoverAnimation: this.animationStyleSheets.rotateAnimation });
  }

  private handleCloseBtnMouseOut = () => {
    this.setState({ closeBtnHoverAnimation: this.animationStyleSheets.rotateBackAnimation });
  }
}
