import * as React from 'react';

import { BarBase, BarBaseProps, BarBaseState } from './BarBase';

export interface BarControledProps extends BarBaseProps {
  open: boolean;
  onClose: () => void;
}

interface BarControledState extends BarBaseState {
  open: boolean;
}

export class BarControled extends BarBase<BarControledProps, BarControledState> {

  constructor(props: BarControledProps) {
    super(props);

    const { open } = this.props;

    this.state = {
      ...this.state,
      open,
    };
  }

  protected isOpen = () => {
    return this.state.open;
  }

  protected close = () => {
    this.setState({ entryOutAnimation: this.animationStyleSheets.outAnimation }, () => {
      setTimeout(() => {
        this.setState({ open: false });
        this.props.onClose();
      }, 1000 * this.entryOutAnimationDuration);
    });
  }

  componentDidUpdate(prevProps: BarControledProps) {
    if (!this.props.open && prevProps.open) {
      this.setState({ entryOutAnimation: this.animationStyleSheets.outAnimation }, () => {
        setTimeout(() => {
          this.setState({ open: false });
          this.props.onClose();
        }, 1000 * this.entryOutAnimationDuration);
      });
    }
  }
}
