import * as React from 'react';
import { css, StyleSheet } from 'aphrodite';
import classnames from 'classnames';

import { ElementProps } from '../components/Element';
import { MobileButton } from '../../WidgetOptions';
import { button, fontSizes, zIndexTop } from '../../styles';

export interface MobileButtonTriggerProps extends ElementProps {
  value: string;
  color: string;
  backgroundColor: string;
  position: MobileButton;
}

export const MobileButtonTrigger = (props: MobileButtonTriggerProps) => {
  const {
    value,
    className,
    backgroundColor,
    color,
    position,
    ...rest
  } = props;

  const buttonOffset = '2em';

  const styles = StyleSheet.create({
    container: {
      ...button,
      position: 'fixed',
      right: position === 'right' ? buttonOffset : undefined,
      left: position === 'left' ? buttonOffset : undefined,
      bottom: buttonOffset,
      backgroundColor,
      color,
      fontSize: fontSizes.normal,
      padding: '.5em',
      zIndex: zIndexTop - 1,
    },
  });

  return (
    <button { ...rest } className={ classnames(className, css(styles.container)) }>{ value }</button>
  );
};
