/* Combined animations with Aphrodite made like this:

```
      const modalCombinedAnimation = {
        animationName: [ fadein, bounce ],
        animationDuration: '0.5s, 2s',
      };

      const styles = StyleSheet.create({ ...modalCombinedAnimation });
```
*/

export const fadein = {
  from: { opacity: 0 },
  to: { opacity: 1 },
};

export const fadeout = {
  from: { opacity: 1 },
  to: { opacity: 0 },
};

/** Takes initial css translate value in percentages. */
export const slidein = (initialTranslate: { tx: number, ty: number }) => ({
  from: { transform: `translate(${ window.innerWidth }px, ${ initialTranslate.ty }%)` },
  to: { transform: `translate(${ initialTranslate.tx }%, ${ initialTranslate.ty }%)` },
});

/** Takes initial css translate value in percentages. */
export const slideout = (initialTranslate: { tx: number, ty: number }) => ({
  from: { transform: `translate(${ initialTranslate.tx }%, ${ initialTranslate.ty }%)` },
  to: { transform: `translate(${ window.innerWidth }px, ${ initialTranslate.ty }%)` },
});

export const slideinTopToBottom = (initialTranslate: { tx: number, ty: number }, boxHeight: number) => ({
  from: { transform: `translate(${ initialTranslate.tx }%, ${ -boxHeight }px)` },
  to: { transform: `translate(${ initialTranslate.tx }%, ${ initialTranslate.ty }%)` },
});

export const slideinBottomToTop = (initialTranslate: { tx: number, ty: number }, boxHeight: number) => ({
  from: { transform: `translate(${ initialTranslate.tx }%, ${ window.innerHeight + boxHeight }px)` },
  to: { transform: `translate(${ initialTranslate.tx }%, ${ initialTranslate.ty }%)` },
});

export const slideoutBottomToTop = (initialTranslate: { tx: number, ty: number }, boxHeight: number) => ({
  from: { transform: `translate(${ initialTranslate.tx }%, ${ initialTranslate.ty }%)` },
  to: { transform: `translate(${ initialTranslate.tx }%, ${ -boxHeight }px)` },
});

export const slideoutTopToBottom = (initialTranslate: { tx: number, ty: number }, boxHeight: number) => ({
  from: { transform: `translate(${ initialTranslate.tx }%, ${ initialTranslate.ty }%)` },
  to: { transform: `translate(${ initialTranslate.tx }%, ${ window.innerHeight + boxHeight }px)` },
});

/** Takes initial css translate value in percentages. */
export const bounce = (initialTranslate: { tx: number, ty: number }) => ({
  '0%': { transform: `translate(${ initialTranslate.tx }%, ${ initialTranslate.ty }%)` },
  '25%': { transform: `translate(${ initialTranslate.tx }%, ${ initialTranslate.ty - 2 }%)` },
  '50%': { transform: `translate(${ initialTranslate.tx }%, ${ initialTranslate.ty }%)` },
  '75%': { transform: `translate(${ initialTranslate.tx }%, ${ initialTranslate.ty - 2 }%)` },
  '100%': { transform: `translate(${ initialTranslate.tx }%, ${ initialTranslate.ty }%)` },
});

export const rotateIn = (deg: number) => ({
  from: { transform: `rotate(0deg)` },
  to: { transform: `rotate(${ deg }deg)` },
});

export const rotateOut = (deg: number) => ({
  from: { transform: `rotate(${ deg }deg)` },
  to: { transform: `rotate(0deg)` },
});

export const animations = {
  fadein,
  fadeout,
  slidein,
  slideout,
  bounce,
  rotateIn,
  rotateOut,
  slideinTopToBottom,
  slideinBottomToTop,
  slideoutTopToBottom,
  slideoutBottomToTop,
};
