import { mediaBreakPointsJs } from '../styles';

export const getPlatform = (): Platform => {
  const { small, medium, large } = mediaBreakPointsJs;
  const mediaQueryLarge = window.matchMedia(large);
  if (mediaQueryLarge && mediaQueryLarge.matches) {
    return 'desktop';
  }

  const mediaQueryMedium = window.matchMedia(medium);
  if (mediaQueryMedium && mediaQueryMedium.matches) {
    return 'mobile';
  }

  return 'desktop';
};

export type Platform = 'desktop' | 'mobile';
